@import (less) "color.less";

@fontSizeTitle: 1.14em;
@fontSizeLabel: 1em;
@fontSizeArrow: 1em;

.panel {
    border: none !important;
    border-radius: 0 !important;
    box-shadow: none;
    .panel-open {
      background-color: @gray !important;
      overflow: visible;
    }
}
.panel-heading {
    height: 3.750em;
    border-radius: 0.125em 0.125em 0px 0px !important;
    box-shadow: none;
    background-color: @dark_white !important;
    cursor: pointer;
    &.panel-heading-round{
      border-radius: 0.125em 0.125em 0.125em 0.125em !important;
    }
    .panel-title {
        color: @black;
        font-size: 0.875em !important;
    }
    .panel-title-tooltip {
      margin-top: -0.3125em;
    }
    .accordion-arrow {
        color: @black;
        position: relative;
        font-size: 2em;
        .icon{
          vertical-align: middle;
        }
    }
    .label {
      color: @black !important;
    }
}

.panel-body {
    border: none !important;
    box-shadow: none;
    background-color: @lightest_gray !important;
    border-radius: 0px 0px 0.125em 0.125em !important;
}

  .panel-collapse{
    .panel-body;
     max-height: 0;
     transition: max-height 0.15s ease-out;
     overflow: hidden;
    &.in{
      max-height: 1000em;
      transition: max-height 0.25s ease-in;
    }
  }

.panelTitleLayout{
      display:table;
      table-layout:fixed;
      width:100%;
      height: 3em;
    }
.panelTitleItemLayout{
      display:table-cell;
      vertical-align: middle;
}

.titlefont{
  overflow:hidden;
  white-space:nowrap;
  text-overflow:ellipsis;
}
    

      
.nbp-panel-primary {
  >.panel-heading {
    position: relative;
    >.panel-title{
      .panelTitleLayout;
      > .accordion-icon{
        .panelTitleItemLayout;
        text-align:center;
        font-size: 2em;
        width:2.3125em;
        padding-right:0.3125em;
        ::before{
          vertical-align: middle;
        }
      }
      >.title-container{
        .panelTitleItemLayout;
        >.title{
          .titlefont;
          font-size: @fontSizeTitle;
          font-weight: 400;
        }
        >.subtitle{
          .titlefont;
          font-size: @fontSizeTitle;
          font-weight: 700;
        }
      }
      >.label{
        .panelTitleItemLayout;
        .titlefont;
        font-size:@fontSizeLabel;
        font-weight: 400;
        text-align: right;
        
      }
      .accordion-arrow{
        .panelTitleItemLayout;
        text-align:center;
        font-size: 1.5em;
        width:25px;
      }
    }
    
  }
}

.panel-default{
  >.panel-heading {
      background-color: @dark_white !important;
    }
  >.panel-open{
    background-color: @gray !important;
  }
}

:host-context(.contrasto-on) {
    .panel {
        .panel-open {
          background-color: @white !important;
          color:@black;
          border: none !important;
          // border: 1px solid @black !important;
          .panel-title {
            color: @black;
          }
          .stringa {
            color:@black;
          }
          .accordion-arrow {
            color:@black;
          }
        }
    }
    .panel-heading {
        background-color: @black !important;
        .panel-title {
          color: @white;
        }
        .accordion-arrow {
            color: @white;
        }
        .label {
          color: @white !important;
        }
    }

    .panel-body {
        background-color: @white !important;
        border: none !important;
        // border-top: none !important;
        // border-right: 0.125em solid @black !important;
        // border-bottom: 0.125em solid @black !important;
        // border-left: 0.125em solid @black !important;
        color:@black;
    }
    .accordion-green-header {
        >.panel-heading {
          background-color: @black !important;
        }
        >.panel-open {
          background-color: @white !important;
          color:@black;
          border:1px solid @black !important;
        }
    }
}
